<template>
  <div>
    <!-- <headerBar :header-data="headerData" /> -->

    <!-- header begin -->
    <div class="nav-bar">
      <div class="nav-bar-left" @click="goTbGzrz()">
        <i class="iconfont iconicon-back" />
      </div>
      <div class="nav-bar-title">查看工作日志</div>
      <div class="nav-bar-right" @click="showSearchPopup()">
        <i class="iconfont iconsousuo" />
      </div>
    </div>
    <!-- header end -->

    <div class="container">

      <van-collapse v-model="activeNames">
        <common-list
          ref="dida_list"
          class="common-list"
          :post-url="list.postUrl"
          :req-data="list.reqData"
          :data-name="list.dataName"
          :apptype-show="false"
          @returnData="getPrizeLog($event)"
        >
          <!-- 循环列表 begin -->
          <div v-for="(item,index) of rzList" :key="index" class="content">
            <div class="common-main box">
              <van-collapse-item :name="index+1">
                <template #title>
                  <div class="work-rizhi-date">
                    <h4>{{ formatDate12(item.rzsj) }} {{ getWeek(item.rzsj) }}</h4>
                    <div class="work-rizhi-zt">
                      <div :class="item.spzt===2?'zt-ysh':item.spzt===1?'zt-wsh':'zt-ygd'">{{ getShzt(item.spzt) }}</div>
                    </div>
                    <div class="work-rizhi-btn">
                      <div v-if="item.spzt===1" class="btn" @click="quRz(item)" @click.stop.native="()=>{}">取回 </div>
                      <div v-if="item.spzt===0" class="btn" @click="tjRz(item)" @click.stop.native="()=>{}">提交 </div>
                    </div>

                  </div>
                </template>
                <div class="work-rizhi">
                  <div v-for="(item1,index1) of item.rzmxArr" :key="index1" class="work-list">
                    <div class="work-xm">
                      <span>{{ getShow(item1) }}</span><em>{{ item1.gzl }}/{{ item1.sdgzl }}</em>
                    </div>
                    <div class="work-content">
                      <p>{{ item1.gznr }}</p>
                      <!-- <van-field
                        v-model="item1.gznr"
                        rows="2"
                        autosize
                        type="textarea"
                        placeholder="请输入日志内容"
                        show-word-limit
                        readonly
                      /> -->
                    </div>
                  </div>
                </div>
                <div v-if="item.spzt===2" class="page-list audit-opinion">
                  <div class="pagelist-left">审核意见</div>
                  <div class="pagelist-right">{{ item.shrmc }}：{{ item.spyj }}</div>
                </div>
              </van-collapse-item>
            </div>
          </div>
          <!-- 循环列表 end -->
        </common-list>
      </van-collapse>

      <searchPick
        :pick-title="searchPick.PickTitle"
        :pick-columns="searchPick.PickColumns"
        @confirmClick="confirmPick($event)"
      />

      <!-- 查询条件  begin -->
      <van-popup v-model="SeachShow" position="top" class="search-popup" overlay-class="transparent-overlay">
        <div class="common-main box">
          <div class="spe-input">
            <!-- <van-cell v-model="shzt" title="审核状态" is-link @click="xzShzt()" /> -->
            <van-cell title="选择开始日期" :value="date" is-link @click="show = true" />
            <van-cell title="选择截止日期" :value="date1" is-link @click="show1 = true" />
            <van-calendar v-model="show" :min-date="minDate" color="#1890ff" @confirm="onConfirm" />
            <van-calendar v-model="show1" :min-date="minDate1" color="#1890ff" @confirm="onConfirm1" />
          </div>
          <div class="search-popup-btn">
            <van-button type="info" round size="large" @click="reset()">重置</van-button>
            <van-button type="info" round size="large" @click="getRzList()">搜索</van-button>
          </div>
        </div>
      </van-popup>
      <!-- 查询条件  end -->

      <Backtop />
    </div>
  </div>
</template>

<script land="script" scoped>
// import headerBar from '@/components/header/headerBar.vue'
import Backtop from '@/components/backtop/backtop' // 返回顶部
import searchPick from '@/components/searchComponents/searchPick.vue'
import { getUserName } from '@/common/js/catch'
import commonList from '@/components/list/commonList1.vue'
import { pageRouter } from '@/common/js/mixin.js'
export default {
  name: 'CkGzrz',
  components: {
    // headerBar,
    Backtop,
    searchPick,
    commonList
  },
  mixins: [pageRouter],
  data() {
    return {
      list: {
        items: [],
        error: false, // 是否在刷新
        postUrl: '',
        reqData: '',
        dataName: '',
        noDataText: ''
      },
      date1: '请选择日期',
      show1: false,
      SeachShow: false,
      maxDate: new Date(2100, 0, 1),
      minDate: new Date(1999, 0, 1),
      minDate1: new Date(1999, 0, 1),
      shzt: '请选择审核状态',
      shztValue: [1, 2, 3],
      // headerData: {
      //   title: '查看工作日志',
      //   leftUrl: 'TbGzrz',
      //   leftText: ' '
      // },
      date: '请选择日期',
      show: false,
      activeNames: ['1'],
      searchPick: {
        PickTitle: '请选择审核状态',
        PickColumns: [{
          label: '初始化',
          value: 0
        }, {
          label: '已提交',
          value: 1
        }, {
          label: '已审核',
          value: 2
        }
        ]
      },
      ksrq: '',
      jzrq: '',
      rzList: []
    }
  },
  created() {
    this.getGzrzLists()
  },
  mounted() {
    this.getCurrentWeek()
  },
  methods: {
    getShow(rzxx) {
      if (rzxx.xmmc === '请选择项目' || !rzxx.xmmc) {
        return rzxx.gzxzmc
      } else {
        return rzxx.xmmc
      }
    },
    reset() {
      this.date = '请选择日期'
      this.date1 = '请选择日期'
      this.ksrq = ''
      this.jzrq = ''
    },
    getCurrentWeek() {
      var nowTime = new Date().getTime()
      // getDate()  如果是周日返回0
      var day = new Date().getDay() === 0 ? 7 : new Date().getDay()
      var oneDayTime = 24 * 60 * 60 * 1000

      // 显示周一
      var MondayTime = nowTime - (day - 1) * oneDayTime
      // 显示周日
      var SundayTime = nowTime + (7 - day) * oneDayTime

      // 初始化日期时间
      var monday = new Date(MondayTime)
      var sunday = new Date(SundayTime)
      this.ksrq = this.formatDate1(monday)
      this.jzrq = this.formatDate1(sunday)
      const reqData = {
        ksrq: this.formatDate1(monday),
        jzrq: this.formatDate1(sunday),
        ryid: getUserName(),
        shbz: this.shztValue
      }
      this.$refs.dida_list.onRefresh(reqData)
    },
    tjRz(item) {
      this.$store.dispatch('erp/saveGzrzAction', {
        rq: item.rzsj,
        ryid: getUserName(),
        submit: true,
        rzmxArr: item.rzmxArr
      }).then(res => {
        this.$store.commit('erp/SET_ISFIRST', false)
        this.$toast('提交日志成功！')
        const reqData = {
          ksrq: this.ksrq,
          jzrq: this.jzrq,
          ryid: getUserName(),
          shbz: this.shztValue
        }
        this.$refs.dida_list.onRefresh(reqData)
      }).catch(error => {
        this.$toast(error)
      })
    },
    quRz(item) {
      this.$store.dispatch('erp/getBackRzAction', {
        ryid: getUserName(),
        rq: item.rzsj
      }).then(res => {
        this.$store.commit('erp/SET_ISSUBMIT', false)
        this.$store.commit('erp/SET_RQ', item.rzsj)
        this.$store.commit('erp/SET_RQ1', this.formatDate12(item.rzsj))
        this.$store.commit('erp/SET_LIST', item.rzmxArr)
        this.$toast('日志取回成功！')
        this.$router.push('TbGzrz')
      })
    },
    getShzt(shzt) {
      if (shzt === 1) {
        return '未审核'
      }
      if (shzt === 2) {
        return '已审核'
      }
      if (shzt === 3) {
        return '已归档'
      }
    },
    getGzrzLists() {
      this.list.postUrl = 'erp/getGzrzAction'
      this.list.reqData = {
        ksrq: this.ksrq,
        jzrq: this.jzrq,
        ryid: getUserName(),
        shbz: this.shztValue
      }
      this.list.dataName = 'gzrzArr'
    },
    getPrizeLog(data) {
      this.rzList = data
    },
    getWeek(date) {
      date = date + ''
      const str = date.slice(0, 4) + '-' + date.slice(4, 6) + '-' + date.slice(6, 8)
      const temp = new Date(str)
      if (temp.getDay() === 1) {
        return '周一'
      }
      if (temp.getDay() === 2) {
        return '周二'
      }
      if (temp.getDay() === 3) {
        return '周三'
      }
      if (temp.getDay() === 4) {
        return '周四'
      }
      if (temp.getDay() === 5) {
        return '周五'
      }
      if (temp.getDay() === 6) {
        return '周六'
      }
      if (temp.getDay() === 0) {
        return '周日'
      }
    },
    getRzList() {
      this.SeachShow = false
      const reqData = {
        ksrq: this.ksrq,
        jzrq: this.jzrq,
        ryid: getUserName(),
        shbz: this.shztValue
      }
      this.$refs.dida_list.onRefresh(reqData)
      // this.$store.dispatch('erp/getGzrzAction', {
      //   ksrq: this.ksrq,
      //   jzrq: this.jzrq,
      //   ryid: getUserName(),
      //   shbz: this.shztValue
      // }).then(res => {
      //   this.rzList = res
      // }).catch(error => {
      //   this.$toast(error)
      // })
    },
    confirmPick(pick) {
      this.shzt = pick.label
      this.shztValue = pick.value
      if (this.ksrq && this.jzrq) {
        this.getRzList()
      }
    },
    xzShzt() {
      this.$bus.$emit('showPick', true)
    },
    formatDate12(date) {
      date = date + ''
      return date.slice(0, 4) + '-' + date.slice(4, 6) + '-' + date.slice(6, 8)
    },
    formatDate(date) {
      return `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`
    },
    formatDate1(date) {
      const year = date.getFullYear()
      let month = ''
      let day = ''
      if ((date.getMonth() + 1) < 10) {
        month = '0' + (date.getMonth() + 1)
      } else {
        month = date.getMonth() + 1
      }
      if (date.getDate() < 10) {
        day = '0' + (date.getDate())
      } else {
        day = date.getDate()
      }
      return year + '' + month + day
    },
    onConfirm(date) {
      this.ksrq = this.formatDate1(date)
      this.date = this.formatDate(date)
      // this.minDate1 = date
      this.show = false
    },
    onConfirm1(date) {
      this.jzrq = this.formatDate1(date)
      this.date1 = this.formatDate(date)
      // this.maxDate = date
      this.show1 = false
    },
    goTbGzrz() {
      this.$router.push({
        name: 'TbGzrz'
      })
    },
    showSearchPopup() {
      this.reset()
      this.SeachShow = true
    }
  }
}
</script>

<style scoped>
.van-collapse-item--border::after{display: none;}
.common-main{padding: 5px 10px;}
.van-hairline--top-bottom::after, .van-hairline-unset--top-bottom::after {border-width: 0;}
</style>
